const routers = [
  {
    path: '/',
    meta: {
      title: '我的短视频'
    },
    component: (resolve) => require(['./index.vue'], resolve)
  },
  {
    path: '/video/:pic/:video',
    meta: {
      title: '我的短视频'
    },
    component: (resolve) => require(['./index.vue'], resolve)
  }
];
export default routers;



