import Vue from 'vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import Routers from './router';
import App from './App.vue';

// 引入mockjs
process.env.MOCK && require('../mock');

Vue.use(VueRouter);
Vue.use(VueResource);

// 路由配置
const RouterConfig = {
  mode: 'hash',
  routes: Routers
};

const router = new VueRouter(RouterConfig);

new Vue({
  el: '#app',
  router: router,
  render: h => h(App)
});



